.advantages {
  position: relative;
  z-index: 2;

  overflow: hidden;

  background-color: $color-solitude;

  padding: 120px 0;
  padding-top: 110px;

  @include vp-1023 {
    padding-top: 90px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 56px;
    padding-bottom: 65px;
  }

  img {
    width: 100%;
    height: 100%;

    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 48px;

    align-self: center;

    font-weight: 600;
    font-size: 28px;
    line-height: 42px;

    @include vp-1023 {
      font-size: 26px;
      line-height: 39px;
    }

    @include vp-767 {
      margin-bottom: 31px;

      font-size: 24px;
      line-height: 36px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;

    list-style: none;
    font-size: 14px;
    line-height: 20px;

    @include vp-1023 {
      gap: 30px;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    padding: 30px;
    padding-bottom: 45px;

    display: flex;
    flex-direction: column;

    background-color: $color-alice-blue;

    @include vp-1023 {
      padding: 26px 16px 20px 20px;
    }
  }

  &__icon {
    margin-bottom: 19px;

    @include vp-1023 {
      margin-bottom: 20px;

      height: 26px;
    }

    @include vp-767 {
      margin-bottom: 19px;
    }

    &--ship {
      @include vp-1023 {
        width: 21px;
      }
    }

    &--check {
      @include vp-1023 {
        width: 27px;
      }
    }

    &--ship-left {
      @include vp-1023 {
        width: 36px;
      }

      @include vp-767 {
        position: relative;
        top: -1px;
        left: -4px;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 9px;

    font-weight: 500;
    font-size: 18px;
    line-height: 27px;

    @include vp-1023 {
      line-height: 25px;
    }

    @include vp-767 {
      max-width: 170px;
    }

    &--zodiac {
      @include vp-1023 {
        margin-bottom: 20px;
      }

      @include vp-767 {
        margin-bottom: 8px;
      }
    }
  }

  &__text {
    margin: 0;
    max-width: 290px;

    br {
      display: none;

      @include vp-767 {
        display: block;
      }
    }

    &--small-container {
      max-width: 280px;
    }

    @include vp-1023 {
      min-width: 169px;
    }

    @include vp-767 {
      min-width: 223px;
    }
  }
}
