.header {
  background-color: $color-prussian-blue;

  &__wrapper {
    position: relative;
    z-index: 2;

    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 35px 0;

    @include vp-1023 {
      padding-top: 30px;
    }

    @include vp-767 {
      padding-top: 20px;
    }
  }

  &__logo {
    &--no-js {
      @include vp-767 {
        margin-bottom: 20px;
      }
    }
  }

  &__button {
    display: none;

    @include vp-767 {
      width: 24px;
      height: 18px;

      padding: 5px;

      display: block;
      position: relative;

      background-color: transparent;
      border: none;
      border-bottom: 2px solid $color-alice-blue;

      &::before,
      &::after {
        position: absolute;
        content: "";

        width: 24px;
        height: 2px;

        background-color: $color-alice-blue;
      }

      &::before {
        top: 4px;
        left: 0;
      }

      &::after {
        top: 10px;
        left: 0;
      }
    }
  }
}
