  opacity: 0.3;
}
.form-wrapper__submit {
  padding: 11px 0 11px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  border: none;
  color: #011c40;
  transition: 0.6s ease;
  background-color: #f9fbfd;
  cursor: pointer;
}
.form-wrapper__submit:focus {
  outline: none;
  opacity: 0.7;
}
.form-wrapper__submit:hover {
  opacity: 0.5;
}
.form-wrapper__submit:active {
  opacity: 1;
}

.checkbox {
  margin-bottom: 34px;
  padding-left: 23px;
  display: flex;
  flex-direction: column;
  position: relative;
  font-size: 10px;
  line-height: 13px;
}
@media (max-width: 767px) {
  .checkbox {
    margin-bottom: 24px;
  }
}
.checkbox .input-message {
  position: absolute;
  top: 10px;
}
@media (max-width: 767px) {
  .checkbox .input-message {
    top: 28px;
  }
}
.checkbox__control-mark {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f9fbfd;
}
.checkbox__control-mark::before, .checkbox__control-mark::after {
  width: 1px;
  position: absolute;
  background-color: #101a56;
}
.checkbox__control-mark::before {
  top: 5px;
  left: 5px;
  height: 7px;
  transform: rotate(-38deg);
}
.checkbox__control-mark::after {
  top: 4px;
  left: 10px;
  height: 9px;
  transform: rotate(45deg);
}
input:checked + .checkbox__control-mark::before, input:checked + .checkbox__control-mark::after {
  content: "";
}
.checkbox label:hover,
.checkbox label:focus-within {
  opacity: 0.7;
}

