.navigation {
  position: relative;
  z-index: 2;

  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $color-alice-blue;

  margin: 0;
  padding: 0;

  display: flex;
  flex-wrap: wrap;

  list-style: none;

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  &--footer {
    @include vp-1023 {
      font-size: 12px;
      line-height: 15px;
    }

    @include vp-767 {
      flex-direction: column;
    }
  }

  &--modal {
    flex-direction: column;

    color: $color-tangaroa;
  }

  &__item {
    margin-right: 33px;
    margin-bottom: 10px;

    &:last-child {
      margin-right: 0;
    }

    @include vp-1023 {
      margin-right: 23px;
    }

    @include vp-767 {
      margin: 0;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &--footer {
      @include vp-767 {
        margin: 0;
      }
    }
  }

  &__link {
    display: block;
    color: inherit;

    &:focus {
      outline: 2px solid $color-alice-blue;
      border-radius: 1px;
      outline-offset: 5px;
    }

    &:hover {
      scale: 1.2;
      transition: $trans-600;
    }

    &:active {
      scale: 0.9;
    }

    @include vp-767 {
      padding: 10px 0 8px 10px;
    }

    &--footer {
      padding: 9px 0;

      @include vp-767 {
        padding-left: 0;
      }
    }

    &--modal {
      font-size: 18px;
      line-height: 22px;
    }
  }
}
